.main {
    max-width: 80%;
}

.bookCompilation {
    margin: 5rem 0;

}


.bookCompilationTitle {
    font-size: 2.1rem;
    font-family: 'Libre Baskerville', serif;
    text-align: left;
    margin: 1rem;
}
.bookCompilationTitle:hover {
    color: rgb(59, 65, 71);
}

.bookCompilationWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    flex-direction: row;
    margin: 1rem 0;
    gap: .7rem;
}
@media (max-width: 820px) {
    .bookCompilationWrapper {
        flex-direction: column;
    }
    .bookCompilationTitle {
        font-size: 1.7rem;
    }
}