.loader {
    position: fixed;
    left: 47vw;
    border: 16px solid #f3f3f3;
    border-top: 16px solid #3586ff;
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
    animation: spin 2s linear infinite;
    z-index: 20000;
}

@keyframes spin {
    0% { transform: rotate(0deg); }
    100% { transform: rotate(360deg); }
}